.Background {
  background: linear-gradient(rgba(0, 0, 0, 1), rgba(0, 0, 0, 1), rgba(0, 0, 0, 1), rgba(28, 47, 31, 1));
  margin: 0%;
  padding: 0%;
  border: 0%;
  padding-bottom: 50px;
}

.Article {
  margin-left: 10%;
  margin-right: 10%;
  background: #466C4C;
  color: #E9FCE7;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;

}

.Spacer {
  background: #466C4C;
    font-family: 'Iowan Old Style', 'Palatino Linotype', 'URW Palladio L', P052, serif;
    color: #E9FCE7;
    line-height: 10px;
    font-size: 1px;
    width: 95%;
    border-right: 10px;
    margin-right: 10px;
    padding-right: 10px;
    border-left: 10px;
    margin-left: 10px;
    padding-left: 10px;
}

.Header {
  background: #466C4C;
  font-family: 'Iowan Old Style', 'Palatino Linotype', 'URW Palladio L', P052, serif;
  color: #E9FCE7;
  font-size: 27px;
  line-height: 40px;
}

.header {
  user-select: none;
  background: #466C4C;
  font-family: 'Iowan Old Style', 'Palatino Linotype', 'URW Palladio L', P052, serif;
  color: #E9FCE7;
  line-height: 30px;
  font-size: 20px;
  width: 95%;
  border-right: 10px;
  margin-right: 10px;
  padding-right: 10px;
  border-left: 10px;

  padding-left: 10px;
}

.Green {
  background: #466C4C;
}

.Rank {
  text-align: center;
  background: #466C4C;
  font-family: 'Iowan Old Style', 'Palatino Linotype', 'URW Palladio L', P052, serif;
  color: #E9FCE7;
  font-size: 27px;
  line-height: 40px;
  color: #E9FCE7;
  margin: 10px;
  border-bottom: 100px;
}

.Link {
  background: #466C4C;
}



.expanded{
  display: block;
  color: #E9FCE7;
  font-family: 'Iowan Old Style', 'Palatino Linotype', 'URW Palladio L', P052, serif;
  background: #466C4C;
  margin: 20px;
  font-size: 18px;
  line-height: 25px;
}

.collapse{
  display: none;
}